<template>
    <div class="user-profile extend">
        <div v-if="isLoading" class="loader-wrap loader-wrap-inside">
            <loader></loader>
        </div>
        <template v-else>
            <div class="user-profile__content">
                <div v-if="numPhotos > 0" class="user-profile__photo"
                     :style="{backgroundImage: `url(${getPhotoUrl(0)})`}"
                     @click="openPhoto(0)">
                </div>
                <div v-else class="user-profile__photo"
                     :style="{backgroundImage: `url(${user.avatars.s4})`}"></div>
                <div class="user-profile__sticky" @click="$nav.back()">
                    <div class="user-profile__exit"><i class="ico ico_exit"></i></div>
                </div>
                <div class="user-profile__title">
                    <div class="user-profile__name-wrap">
                        <div class="ellipsis">{{user.name}}</div>
                        <div class="user-profile__age" v-if="user.age">, {{user.age}}</div>
                    </div>
                    <span v-if="user.is_online" class="online-status"></span>
                </div>
                <div v-if="user.about" class="user-profile__section">
                    <p>{{user.about}}</p>
                </div>
                <div v-else-if="user.about_auto" class="user-profile__section">
                    <p>{{user.about_auto}}</p>
                </div>
                <div v-if="user.goals && user.goals.length > 0" class="user-profile__section">
                    <h2>{{$t('user_profile.goals')}}</h2>
                    <p>{{user.goals.join(', ')}}</p>
                </div>
                <div v-if="numPhotos == 2 || numPhotos >= 4" class="user-profile__photo"
                     :style="{backgroundImage: `url(${getPhotoUrl(1)})`}"
                     @click="openPhoto(1)"></div>
                <div v-if="user.interests" class="user-profile__section">
                    <h2>{{$t('user_profile.interests')}}</h2>
                    <p class="user-profile__interests-list">
                        <span
                                class="user-profile__interest"
                                v-for="(interest, index) in user.interests"
                                :key="index"
                                :class="{'user-profile__interest_match': interest.matches}"
                        >{{interest.text}}</span>
                    </p>
                </div>
                <div v-if="numPhotos >= 3" class="user-profile__photos">
                    <div class="user-profile__preview-wrap">
                        <div class="user-profile__preview"
                             :style="{backgroundImage: `url(${getPhotoUrl(numPhotos == 3 ? 1 : 2)})`}"
                             @click="openPhoto(numPhotos == 3 ? 1 : 2)"></div>
                    </div>
                    <div class="user-profile__preview-wrap">
                        <div class="user-profile__preview"
                             :style="{backgroundImage: `url(${getPhotoUrl(numPhotos == 3 ? 2 : 3)})`}"
                             @click="openPhoto(numPhotos == 3 ? 2 : 3)"></div>
                        <div v-if="numPhotos > 4" class="user-profile__more-photos"
                             v-html="$t('user_profile.more', {count: numPhotos - 3})"></div>
                    </div>
                </div>
                <div v-if="user.job" class="user-profile__section">
                    <h2>{{$t('user_profile.job')}}</h2>
                    <p>{{user.job}}</p>
                </div>
                <div v-if="user.about_auto && user.about" class="user-profile__section">
                    <h2>{{$t('user_profile.about')}}</h2>
                    <p>{{user.about_auto}}</p>
                </div>
                <div class="user-profile__section">
                    <h2>{{$t('user_profile.city')}}</h2>
                    <p>{{user.city_name}}</p>
                </div>
                <div v-if="!isMe" class="user-profile__complain">
                    <button @click="complain">{{$t('user_profile.complain')}}</button>
                </div>
            </div>
            <div v-if="!isMe && user.can_meet" class="user-profile__extra">
                <profile-actions v-if="!focused" @like="likeAction" @dislike="dislikeAction"></profile-actions>
                <button v-if="dialogLockedByDailyLimit" class="user-profile__dialog-locked btn_primary"
                        @click="showOffer" v-touch>
                    {{ dialogLockedPlaceholder }}
                </button>
                <require-photo v-else-if="dialogRequiresPhoto" :user="user"/>
                <inline-message v-else @focus="focused = true" @blur="focused = false" @submit="sendMessage"
                                :placeholder="messagePlaceholder"></inline-message>
            </div>
        </template>
    </div>
</template>

<script>
    import Loader from '@/components/Loader';
    import ProfileActions from '@/components/profile/ProfileActions';
    import InlineMessage from '@/components/InlineMessage';
    import RequirePhoto from './RequirePhoto';
    import {getUser} from '@/api/meeting';
    import {postMessage} from '@/api/chat';
    import {mapState, mapGetters, mapMutations} from 'vuex';
    import feed from '@/feed/';
    import {GENDER_MAN, SOURCE_VIP_CHAT_REQUEST_LIMIT} from '../../consts';
    import photoviewer from '@/popups/photoviewer';
    import profileInterstitial from '../../advert/profileInterstitial';

    let _selfVM = '';

    export default {
        name: "UserProfile",
        components: {Loader, ProfileActions, InlineMessage, RequirePhoto},
        props: ['id', 'card'],
        data() {
            return {
                me: this.$store.state.config.user,
                user: null,
                focused: false,
                isLoading: false
            }
        },
        computed: {
            numPhotos() {
                if (!this.user.photo) {
                    return 0
                }

                return this.user.photo.length;
            },
            messagePlaceholder() {
                return this.$t('user_profile.dialog_placeholder', {pronouns: this.pronouns});
            },
            dialogLockedPlaceholder() {
                return this.$t('user_profile.dialog_locked', {pronouns: this.pronouns});
            },
            pronouns() {
                return this.user.gender === GENDER_MAN ? this.$t('personal_pronouns.him') : this.$t('personal_pronouns.her');
            },
            isMe() {
                return this.user.id == this.me.id;
            },
            dialogRequiresPhoto() {
                if (this.hasVip) {
                    return false;
                }

                return this.user.dialog_requires_photo && this.needUploadPhoto;
            },
            dialogLockedByDailyLimit() {
                if (this.hasVip || this.user.is_open) {
                    return false;
                }

                return !this.canOpenDialog;
            },
            ...mapState('vip', ['hasVip']),
            ...mapState('feed', ['likeActionLocked', 'viewedCardsAfterPhotoRequest']),
            ...mapGetters('vip', ['trialIsAvailable']),
            ...mapGetters('feed', ['needUploadPhoto', 'numCardsBeforeRequestPhoto', 'canOpenDialog'])
        },
        created() {
            let intent = this.$nav.activeIntent;
            let user = this.card || intent.options.user;

            _selfVM = this;

            if (user) {
                this.user = user;
                this.trackStatEvents();
            } else {
                this.isLoading = true;

                let id = this.id || decodeURIComponent(intent.query.id);
                getUser({user_id: id, include: 'photos,interests'}).then(response => {
                    this.user = response.objects[0];
                    this.trackStatEvents();
                    this.isLoading = false;
                })
            }
        },
        mounted() {
            if (this.hasVip) {
                return;
            }

            profileInterstitial.load();
        },
        hidePopupHandler() {
            if (_selfVM.hasVip || _selfVM.isMe) {
                return;
            }

            if (profileInterstitial.shouldPresent()) {
                profileInterstitial.present();
            }
        },
        methods: {
            likeAction() {
                if (this.needUploadPhoto && this.viewedCardsAfterPhotoRequest >= this.numCardsBeforeRequestPhoto) {
                    feed.showPhotoUpload(this.user);
                    this.lockLikeAction();

                    return;
                }

                feed.likeAction(this.user);
                this.$store.commit('feed/removeCardById', this.user.id);
                this.$nav.back();
            },
            dislikeAction() {
                if (this.likeActionLocked) {
                    this.unlockLikeAction();
                    this.resetPhotoRequestCounter();
                }

                feed.dislikeAction(this.user);
                this.$store.commit('feed/removeCardById', this.user.id);
                this.$nav.back();
            },
            sendMessage(message) {
                postMessage({
                    contactId: this.user.id,
                    message: message
                }).then(response => {
                    this.$push.emit('notification', {
                        data: {
                            text: this.$t('notification.msg'),
                            ava: this.user.avatars.s1,
                            style: 'yellow'
                        }
                    });
                }).catch(error => {
                    feed.showMessageLimit(this.user);
                });
            },
            trackStatEvents() {
                if (this.isMe) {
                    return;
                }

                this.$nativeStat.trackFirebaseEvent('item_action', {screen: 'Profile', action: 'View'});
                this.$nativeStat.trackMetricaEvent('item_action', {'Profile': 'View'});
                this.$nativeStat.trackAppsFlyerEvent('fs_action', {af_content_type: 'Profile', ap_action: 'View'});

                this.$nativeStat.trackCommonEvent('profile_view', {user_id: this.user.id});
                this.$nativeStat.trackAppsFlyerEvent('ap_profile_view', {ap_user_id: this.user.id});

                if (this.dialogLockedByDailyLimit) {
                    this.$nativeStat.trackFirebaseEvent('item_action', {
                        screen: 'Profile',
                        action: 'Chat Request Limit'
                    });
                    this.$nativeStat.trackMetricaEvent('item_action', {'Profile': 'Chat Request Limit'});
                    this.$nativeStat.trackAppsFlyerEvent('fs_action', {
                        af_content_type: 'Profile',
                        ap_action: 'Chat Request Limit'
                    });
                }
            },
            getPhotoUrl(index) {
                return this.user.photo[index].urls.s4;
            },
            openPhoto(index) {
                let popup;
                let photo = this.user.photo[index];

                this.$nav.pushInstant({
                    activate: animated => {
                        popup = photoviewer.open({
                            fetch: () => {
                                return new Promise(resolve => {
                                    let photos = this.user.photo.map(item => {
                                        return {
                                            id: item.id,
                                            url: item.urls.o
                                        }
                                    });
                                    resolve(photos);
                                });
                            },
                            initialId: photo && photo.id,
                            urlAlias: "url",
                            animated
                        });
                    },
                    unload: animated => {
                        photoviewer.close(popup, animated);
                    }
                });
            },
            complain() {
                feed.complain(this.user).then((response = {}) => {
                    if (response.success) {
                        feed.skipAction(this.user);
                        this.$nav.back();
                    }
                });
            },
            showOffer() {
                feed.showVipFullOffer({source: SOURCE_VIP_CHAT_REQUEST_LIMIT});
            },
            ...mapMutations('feed', ['lockLikeAction', 'unlockLikeAction', 'resetPhotoRequestCounter'])
        }
    }
</script>
