<template>
    <div class="messenger-contact__message">
        <div class="messenger-contact__message-text" v-html="text"></div>
        <div v-if="contact.new > 0" class="messenger-contact__count">{{contact.new | counterLimit}}</div>
    </div>
</template>

<script>
    export default {
        props: ['contact'],
        computed: {
            text() {
                switch (this.type) {
                    case 'image': return 'Фото';
                    default: return this.contact.lastmess.text;
                }
            },
            type() {
                if (this.contact.lastmess.attaches) {
                    for (let key in this.contact.lastmess.attaches) {
                        if (this.contact.lastmess.attaches[key].type === 'image') {
                            return 'image';
                        } else {
                            return 'other';
                        }
                    }
                }
                return 'text';
            }
        }
    };
</script>
