<template>
    <div class="messenger-contacts-wrapper" ref="scrollWrapper">
        <div class="messenger-contacts">
            <div v-if="isEmpty && settings.emptyMessage" class="messenger-contacts__empty">
                <div class="messenger-contacts__empty-title">{{settings.emptyMessage}}</div>
            </div>
            <contact
                    v-for="contact in contacts"
                    :key="contact.id"
                    :contact="contact"
                    @click="$emit('select', contact)"/>

            <contacts-loader v-if="isLoading"/>
            <suggestions v-else-if="settings.dialogsPromo && hasSuggestions" :users="suggestions"></suggestions>
        </div>
    </div>
</template>

<script>
    import {mapState, mapActions} from 'vuex';
    import Suggestions from './Suggestions';
    import Contact from './Contact';
    import ContactsLoader from './ContactsLoader';

    export default {
        components: {ContactsLoader, Suggestions, Contact},
        props: ['extra-params', 'settings'],
        data() {
            return {};
        },
        computed: {
            ...mapState('messenger', {
                contacts: state => state.contacts,
                isLoading: state => state.contactsIsLoading,
                hasNext: state => state.contactsHasNext
            }),
            ...mapState('feed', ['suggestions']),
            hasSuggestions() {
                return this.suggestions.length
            },
            isEmpty() {
                return !this.isLoading && !this.contacts.length;
            }
        },
        watch: {
            extraParams(newValue) {
                this.loadContactsWithNewExtraParams(newValue);
            }
        },
        mounted() {
            this.loadContactsWithNewExtraParams(this.extraParams);

            this.$refs.scrollWrapper.addEventListener('scroll', this.onScroll);
        },
        methods: {
            ...mapActions('messenger', ['loadContactsBatch', 'loadContactsWithNewExtraParams']),
            onScroll() {
                if (!this.hasNext || this.isLoading) {
                    return;
                }

                const wrapperHeight = this.$refs.scrollWrapper.clientHeight;
                const scrollHeight = this.$refs.scrollWrapper.scrollHeight;
                const scrollValue = this.$refs.scrollWrapper.scrollTop;

                if (scrollHeight - wrapperHeight - scrollValue < 20) {
                    this.loadContactsBatch();
                }
            }
        }
    };
</script>
