<template>
    <div class="app">
        <nav-view></nav-view>
        <nav-bar v-if="currentTabId !== 0"></nav-bar>
        <banner-placeholder v-if="!bannerIsShown"></banner-placeholder>
        <popups></popups>
        <push></push>
    </div>
</template>

<script>
    import Loader from './components/Loader';
    import Popups from './components/Popups';
    import Push from './components/push/Push';
    import feed from './feed/';
    import NavBar from './components/navigation/NavBar';
    import BannerPlaceholder from './components/advert/BannerPlaceholder';
    import interstitials from './advert/interstitials';
    import bottomBanner from './advert/bottomBanner';
    import bridge from './bridge';
    import {getConfig} from './api/meeting';
    import {mapState, mapGetters} from 'vuex';
    import {Trans} from './plugins/Translation';
    import {PLACEMENT_FEED, PLACEMENT_PROFILE, PLACEMENT_INCOMING, AVATAR_STATUS_APPROVED} from './consts';
    import serverTime from './lib/serverTime';
    import messengerDataSource from './messenger/datasource';
    import messengerSocket from './messenger/socket';
    import messenger from './messenger';

    export default {
        name: 'app',
        components: {Loader, NavBar, BannerPlaceholder, Popups, Push},
        computed: {
            ...mapState(['currentTabId', 'bannerIsShown']),
            ...mapState('feed', ['moderationStatus']),
            ...mapGetters('feed', ['numNotify'])
        },
        data() {
            return {
                isLoading: true
            }
        },
        created() {
            // this.getApiHost()
            //     .then(response => {
