            //
            //         let curDate = new Date();
            //
            //         return getConfig({
            //             timezone_offset: curDate.getTimezoneOffset() * -1
            //         });
            //     })
            //     .then(response => {
            //         let config = response.objects[0];
            //
            //         if (config.user === undefined) {
            //             bridge.invokeNative('update_access_token');
            //         }
            //
            //         if (response.meta && response.meta.ab !== undefined) {
            //             response.meta.ab.forEach(item => {
            //                 this.$nativeStat.trackMetricaEvent('ab_test_join', item);
            //
            //                 let appsFlyerParams = {};
            //                 for (let key in item) {
            //                     if(item.hasOwnProperty(key)) {
            //                         appsFlyerParams['ap_'+ key] = item[key];
            //                     }
            //                 }
            //                 this.$nativeStat.trackAppsFlyerEvent('ap_ab_test_join', appsFlyerParams);
            //             });
            //         }
            //
            //         return this.loadLocalization({});
            //     })
            //     .then(response => {
            //         this.init(response);
            //         this.isLoading = false;
            //     })
            //     .catch(error => {
            //         console.log('app init error', error);
            //     });
        },
        methods: {
            getApiHost() {
                return new Promise((resolve, reject) => {
                    if (bridge.isSupported()) {
                        bridge.invokeNative('get_api_host', {}, response => {
                            resolve(response);
                        });
                    } else {
                        reject({message: 'get_api_host failed'});
                    }
                });
            },
            loadLocalization(config) {
                const lang = config.lang || Trans.defaultLanguage;

                return new Promise((resolve, reject) => {
                    Trans.changeLanguage(lang).then(response => {

                    }).catch(error => {
                        console.log('loadLocalization error', error);
                    }).finally(() => {
                        resolve(config);
                    });
                });
            },
            init(config) {
                this.$store.state.config = config;

                this.$store.dispatch('feed/init', config);
                this.$store.dispatch('vip/init', config);

                bridge.invokeNative('set_photo_status', {fs_photo: config.avatar_status});
                bridge.invokeNative('set_badge', {value: this.numNotify.toString()});

                serverTime.init(config.server_time || parseInt(new Date().getTime() / 1000));

                // messengerSocket.init(config);

                // messenger.userId = parseInt(config.user.id);
                // messenger.dataSource = messengerDataSource;

                // if (config.adv) {
                //     this.initPlacements(config.adv);
                // }

                let query = this.$nav.activeIntent && this.$nav.activeIntent.query || {};

                // feed.init({...config, ...query});

                // if (config.survey) {
                //     this.$nav.redirect('/app/survey');
                // }

                // if (config.user){
                //     this.$nav.redirect('/app/feed');
                // } else {
                //     this.$nav.redirect('/app/signup');
                // }
            },
            initPlacements(advert) {
                interstitials.init();

                if (advert.feed_swipe) {
                    interstitials.createPlacement({name: PLACEMENT_FEED, options: advert.feed_swipe});
                }

                if (advert.profile_close) {
                    interstitials.createPlacement({name: PLACEMENT_PROFILE, options: advert.profile_close});
                }

                if (advert.incoming_open && advert.incoming_open.place_id > 0) {
                    interstitials.createPlacement({name: PLACEMENT_INCOMING, options: advert.incoming_open});
                }

                if (advert.sticky && advert.sticky.place_id > 0) {
                    bottomBanner.init(advert.sticky);
                    bottomBanner.show();
                }
            }
        },
        watch: {
            moderationStatus(newStatus) {
                if (newStatus === AVATAR_STATUS_APPROVED) {
                    this.$nativeStat.reportMetricaUserProperty({key: 'has_photo', boolean: true});
                    this.$nativeStat.reportFirebaseUserProperty({key: 'has_photo', boolean: true});
                    this.$nativeStat.reportAppsFlyerUserProperty({key: 'fs_photo', boolean: true});
                }
                // TODO удалить когда Android будет поддерживать все 3 метода
                bridge.invokeNative('set_photo_status', {fs_photo: newStatus});
            },
            numNotify(newValue) {
                bridge.invokeNative('set_badge', {value: newValue.toString()});
            }
        }
    }
</script>