<template>
    <div v-if="showing" class="card-item"
         :class="{'card-item__is-current': isCurrent, 'card-item__animated': interactAnimating}"
         :style="{ transform: transformString }" ref="interactElement">
        <div class="card-item__photo" :style="{backgroundImage: `url(${photoUrl})`}"
             @click="$emit('clickProfile')"></div>
        <div class="card-item__like card-item__indicator" :style="isDraggedRight && indicatorStyles">
            {{$t('card.like')}}
        </div>
        <div class="card-item__dislike card-item__indicator" :style="isDraggedLeft && indicatorStyles">
            {{$t('card.dislike')}}
        </div>
        <div class="card-item__extra">
            <card-info :card="card" @click.native="$emit('clickProfile')"></card-info>
            <card-actions @like="likeAction" @dislike="dislikeAction" @message="sendMessage"></card-actions>
        </div>
        <div v-if="card.additional_data" class="card-item__additional" v-html="card.additional_data"></div>
    </div>
</template>

<script>
    import interactMixin from '@/mixins/interact';
    import CardInfo from './CardInfo';
    import CardActions from './CardActions';
    import feed from '@/feed/';
    import {mapState, mapGetters, mapMutations} from 'vuex';

    const MAX_DRAG_DISTANCE = 100;

    export default {
        name: "Card",
        mixins: [interactMixin],
        components: {CardInfo, CardActions},
        data() {
            return {
                showing: true,
                interactMaxRotation: 15
            }
        },
        props: ['card', 'isCurrent'],
        computed: {
            photoUrl() {
                return this.card.avatars.s5;
            },
            isDraggedLeft() {
                return !this.interactAnimating && this.interactPosition.x < 0;
            },
            isDraggedRight() {
                return !this.interactAnimating && this.interactPosition.x > 0;
            },
            absoluteDraggedDistance() {
                return Math.abs(this.interactPosition.x);
            },
            indicatorStyles() {
                return {
                    opacity: Math.min(this.absoluteDraggedDistance / MAX_DRAG_DISTANCE, 1)
                }
            },
            dialogLockedByDailyLimit() {
                if (this.hasVip || this.card.is_open) {
                    return false;
                }

                return !this.canOpenDialog;
            },
            ...mapState('vip', ['hasVip']),
            ...mapState('feed', ['likeActionLocked', 'viewedCardsAfterPhotoRequest']),
            ...mapGetters('feed', ['needUploadPhoto', 'numCardsBeforeRequestPhoto', 'canOpenDialog'])
        },
        mounted() {
            if (this.isCurrent) {
                this.trackViewEvent();
            }
        },
        methods: {
            draggedRight() {
                this.likeAction();
            },
            draggedLeft() {
                this.dislikeAction();
            },
            likeAction() {
                if (this.needUploadPhoto && this.viewedCardsAfterPhotoRequest >= this.numCardsBeforeRequestPhoto) {
                    feed.showPhotoUpload(this.card);

                    this.interactSetPosition({x: 0, y: 0, rotation: 0});
                    this.lockLikeAction();

                    return;
                }

                this.playCard();

                this.interactSetPosition({
                    x: this.interactOutOfSightXCoordinate,
                    rotation: this.interactMaxRotation,
                });

                this.$emit('likeAction');
                this.hideCard();
            },
            dislikeAction() {
                if (this.likeActionLocked) {
                    this.unlockLikeAction();
                    this.resetPhotoRequestCounter();
                }

                this.playCard();

                this.interactSetPosition({
                    x: -this.interactOutOfSightXCoordinate,
                    rotation: -this.interactMaxRotation,
                });

                this.$emit('dislikeAction');
                this.hideCard();
            },
            playCard() {
                this.interactUnsetElement();
            },
            hideCard() {
                setTimeout(() => {
                    this.showing = false;
                    this.$emit('hideCard', this.card);
                }, 300);
            },
            sendMessage() {
                if (this.dialogLockedByDailyLimit) {
                    feed.showMessageLimit(this.card);
                } else {
                    feed.openChatWithUser(this.card.id);
                }
            },
            trackViewEvent() {
                this.$nativeStat.trackFirebaseEvent('item_action', {screen: 'Photo Feed', action: 'View'});
                this.$nativeStat.trackMetricaEvent('item_action', {'Photo Feed': 'View'});
                this.$nativeStat.trackAppsFlyerEvent('fs_action', {af_content_type: 'Photo Feed', ap_action: 'View'});

                if (!this.card.photo || this.card.photo.length === 0) {
                    return
                }

                let userId = this.card.id;
                let photoId = this.card.photo[0].id;

                this.$nativeStat.trackCommonEvent('photo_feed_view', {user_id: userId, photo_id: photoId});
                this.$nativeStat.trackAppsFlyerEvent('ap_photo_feed_view', {ap_user_id: userId, ap_photo_id: photoId});
            },
            ...mapMutations('feed', ['lockLikeAction', 'unlockLikeAction', 'resetPhotoRequestCounter'])
        },
        watch: {
            isCurrent(newValue) {
                if (newValue) {
                    this.trackViewEvent();
                }
            }
        }
    }
</script>
