<template>
    <div class="meeting-feed">
        <settings @filterMode="showFilter"></settings>
        <feed-limit v-if="hasLimit" :limitData="feedLimit"></feed-limit>
        <empty-feed v-else-if="isEmpty" @filterMode="showFilter"></empty-feed>
        <template v-else>
            <cards
                    :cards="visibleCards"
                    @likeAction="handleLikeAction"
                    @dislikeAction="handleDislikeAction"
                    @hideCard="removeCardFromDeck"
            />
        </template>
        <transition name="fade">
            <filter-setting v-if="filterMode" @close="filterMode = false"/>
        </transition>
    </div>
</template>

<script>
    import Settings from './Settings';
    import Cards from './Cards';
    import Controls from './Controls';
    import EmptyFeed from './EmptyFeed';
    import FeedLimit from './FeedLimit';
    import {mapState, mapGetters} from 'vuex';
    import feed from '@/feed/';
    import {DIRECTION_RIGHT, DIRECTION_LEFT, PLACEMENT_FEED, AVATAR_STATUS_APPROVED} from '../../consts';
    import FilterSetting from './FilterSettings';
    import interstitials from '../../advert/interstitials';

    export default {
        name: 'Feed',
        components: {Settings, Controls, Cards, EmptyFeed, FeedLimit, FilterSetting},
        data() {
            return {
                filterMode: false
            }
        },
        computed: {
            ...mapState('feed', ['visibleCards', 'filter', 'feedLimit', 'viewedCards', 'viewedCardsAfterPhotoRequest']),
            ...mapState('vip', ['hasVip']),
            ...mapGetters('feed', ['currentCard', 'isEmpty', 'hasLimit', 'needUploadPhoto']),
        },
        methods: {
            handleLikeAction() {
                feed.likeAction(this.currentCard);
                feed.showSwipeTutorial(DIRECTION_RIGHT, this.currentCard);

                if (this.currentCard.photo && this.currentCard.photo.length > 0) {
                    let userId = this.currentCard.id;
                    let photoId = this.currentCard.photo[0].id;

                    this.$nativeStat.trackCommonEvent('photo_like', {user_id: userId, photo_id: photoId});
                    this.$nativeStat.trackAppsFlyerEvent('ap_photo_like', {ap_user_id: userId, ap_photo_id: photoId});
                }
            },
            handleDislikeAction() {
                feed.dislikeAction(this.currentCard);
                feed.showSwipeTutorial(DIRECTION_LEFT, this.currentCard);

                if (this.currentCard.photo && this.currentCard.photo.length > 0) {
                    let userId = this.currentCard.id;
                    let photoId = this.currentCard.photo[0].id;

                    this.$nativeStat.trackCommonEvent('photo_dislike', {user_id: userId, photo_id: photoId});
                    this.$nativeStat.trackAppsFlyerEvent('ap_photo_dislike', {ap_user_id: userId, ap_photo_id: photoId});
                }
            },
            removeCardFromDeck() {
                this.$store.commit('feed/removeCurrentCard');
            },
            showFilter() {
                this.filterMode = true;

                this.$nativeStat.trackFirebaseEvent('item_action', {screen: 'Filter', action: 'View'});
                this.$nativeStat.trackMetricaEvent('item_action', {'Filter': 'View'});
                this.$nativeStat.trackAppsFlyerEvent('fs_action', {af_content_type: 'Filter', ap_action: 'View'});
            }
        },
        watch: {
            filter(newFilter, oldFilter) {
                if (!newFilter || !oldFilter) {
                    return;
                }

                if (newFilter.age_from !== oldFilter.age_from || newFilter.age_to !== oldFilter.age_to) {
                    feed.filterBy(newFilter);
                }
            },
            feedLimit(newLimit, oldLimit) {
                if (newLimit == null && oldLimit != null) {
                    feed.resetCards();
                }
            },
            viewedCards(count) {
                let placement = interstitials.getPlacement(PLACEMENT_FEED);

                if (this.hasVip || !placement) {
                    return;
                }

                let frequency = placement.options.interval || 10;
                if (count % frequency === 1) {
                    interstitials.load(PLACEMENT_FEED);
                } else if (count % frequency === 0) {
                    interstitials.present(PLACEMENT_FEED);
                }
            }
        }
    };
</script>

