<template>
    <div class="card-actions">
        <button class="card-actions__btn ignore-form" @click="$emit('dislike')" v-touch>
            <span class="card-actions__ico"><dislike-icon/></span>
        </button>
        <button class="card-actions__btn card-actions__message ignore-form" @click="$emit('message')" v-touch>
            <span class="card-actions__ico"><message-icon/></span>
        </button>
        <button class="card-actions__btn ignore-form" @click="$emit('like')" v-touch>
            <span class="card-actions__ico"><like-icon/></span>
        </button>
    </div>
</template>

<script>
    import LikeIcon from '@/assets/svg/action_like.svg';
    import DislikeIcon from '@/assets/svg/action_dislike.svg';
    import MessageIcon from '@/assets/svg/action_message.svg';

    export default {
        name: "CardActions",
        components: {LikeIcon, DislikeIcon, MessageIcon}
    }
</script>
