<template>
    <div class="signup-step">
        <div>
            <signup-step-back/>
            <div class="signup-step__title">{{$t('signup.age')}}</div>
            <div class="signup-step__field">
                <input class="signup-step__input" :class="{'signup-step__input_invalid': error != null}"
                       type="number"
                       :placeholder="$t('signup.age_placeholder')"
                       :value="userAge"
                       @focus="focused = true" @blur="focused = false"
                       @input="input"
                       @keyup.enter="submit"
                       ref="inputField">

                <div v-if="error" class="signup-step__invalid">{{error}}</div>
            </div>
        </div>

        <div class="btn signup-step__btn ripple ripple_white" :class="{'signup-step__btn_active': checkAge() !== false}" @click="nextStep">{{$t('done')}}</div>
    </div>
</template>

<script>
    import SignupStepBack from '@/components/signup/SignupStepBack';
    import {mapGetters} from 'vuex';

    export default {
        name: "SignupAge",
        components: {SignupStepBack},
        data(){
            return {
                focused: false,
                error: null,
                userAge: '',
                isBtnActive: false
            }
        },
        computed: {
            ...mapGetters('signup', ['params'])
        },
        created(){
            if (this.params.age !== 0){
                this.userAge = this.params.age;
            }
        },
        mounted(){
            this.$refs.inputField.focus();
        },
        methods: {
            input(event){
                this.userAge = event.target.value;

                if (String(this.userAge).length >= 2){
                    this.error = this.userAge < 18 ? this.$t('signup.error_age_min_value') : null;
                }
            },
            back(){
                this.$nav.go(-1);
            },
            nextStep(){
                if (this.checkAge()){
                    this.setAge();
                }
            },
            checkAge(){
                let isValid = false;

                if(+this.userAge > 18){
                    this.isBtnActive = true;

                    isValid = true;
                }

                return isValid;
            },
            setAge(){
                this.$store.commit('signup/setAge', this.userAge);

                this.$nav.push('/app/signup/city');
            }
        }
    }
</script>