import store from '@/store';
import {getContacts, getContact, getMessages, postMessage, readAll} from '@/api/chat';
import nativeStat from '@/lib/statistic/nativeStat';


export default {
    getContacts(params) {
        return getContacts(params).then(response => {
            if (response.meta.suggestions) {
                store.commit('feed/setSuggestions', response.meta.suggestions);
            }

            if (params.offset === 0 && params.filter === undefined) {
                let hasChats = response.objects.length > 0;
                let hasUnreadChats = response.objects.some(item => item.new > 0);

                if (hasChats) {
                    nativeStat.trackFirebaseEvent('item_action', {screen: 'Messenger Chat List', action: 'Has Chats'});
                    nativeStat.trackMetricaEvent('item_action', {'Messenger Chat List': 'Has Chats'});
                    nativeStat.trackAppsFlyerEvent('fs_action', {
                        af_content_type: 'Messenger Chat List',
                        ap_action: 'Has Chats'
                    });
                }

                if (hasUnreadChats) {
                    nativeStat.trackFirebaseEvent('item_action', {
                        screen: 'Messenger Chat List',
                        action: 'Has Unread Chats'
                    });
                    nativeStat.trackMetricaEvent('item_action', {'Messenger Chat List': 'Has Unread Chats'});
                    nativeStat.trackAppsFlyerEvent('fs_action', {
                        af_content_type: 'Messenger Chat List',
                        ap_action: 'Has Unread Chats'
                    });
                }
            }

            return {
                contacts: response.objects,
                hasNext: response.meta.has_next
            }
        });
    },
    getContact(id) {
        return getContact({contactId: id}).then(response => {
            return {
                contact: response.objects[0]
            }
        });
    },
    getMessages(contact, params) {
        return getMessages(params).then(response => {
            const result = {
                messages: [],
                hasPrev: false
            };

            result.hasPrev = response.meta.has_next;
            result.messages = response.objects;

            if (contact.mutual_ts && !result.hasPrev) {
                result.messages.push(formatMutualMessage(contact));
            }
            result.messages.reverse();

            return result;
        });
    },
    postMessage(params) {
        return postMessage(params);
    },
    markReaded(contactId) {
        return readAll({
            contactId: contactId
        });
    },
    removeContact(contactId) {
        const formData = new FormData();
        formData.append('id', contactId);
        formData.append('clear_history', 1);
        formData.append('action', 2);
        return post('/usercontact_chat/remove_contact/', formData);
    }
};

function formatMutualMessage(contact) {
    const message = {};
    message.id = 'mutual';
    message.mutual_ts = contact.mutual_ts;

    return message;
}
