<template>
    <div class="my-profile extend">
        <div class="my-profile__preview" @click="previewProfile">
            <div class="my-profile__photo" :style="{backgroundImage: `url(${photoUrl})`}"></div>
            <div class="my-profile__name ellipsis">{{me.name}}, {{me.age}}</div>
            <button class="my-profile__link">{{$t('profile.link')}}</button>
        </div>
        <button class="my-profile__edit-btn btn_secondary btn_size-m" @click="editProfile" v-touch>
            {{$t('profile.btn_edit')}}
        </button>
        <div v-if="hasVip" class="my-profile__vip">
            <div class="my-profile__vip-text"><i class="ico ico_diamond-small-gray"></i>VIP</div>
            <div class="my-profile__vip-status">{{$t('profile.vip_activated')}}</div>
        </div>
        <div v-else class="my-profile__vip yellow" @click="showOffer" v-touch>
            <div class="my-profile__vip-text"><i class="ico ico_diamond-small"></i>VIP</div>
            <div class="my-profile__vip-status" v-html="$t('profile.btn_vip')"></div>
        </div>
        <button class="my-profile__link" :class="{'my-profile__link_badge': hasSupportAnswer}" @click="settings"><i class="ico ico_gear"></i>{{$t('profile.btn_settings')}}
        </button>
        <transition :appear="isFirstTime" name="slide-down">
            <banner-request-photo v-if="needUploadPhoto" class="my-profile__banner"/>
        </transition>
    </div>
</template>

<script>
    import {mapState, mapGetters} from 'vuex';
    import feed from '@/feed';
    import {SOURCE_VIP_MY_PROFILE, VENDOR_IOS} from '../../consts';
    import BannerRequestPhoto from './BannerRequestPhoto';
    import {getVendor} from '../../lib/browser';
    import {getInfo} from '../../api/profile';

    export default {
        name: "MyProfile",
        components: {BannerRequestPhoto},
        data() {
            return {
                me: this.$store.state.config.user,
                isLoadingStatus: false
            }
        },
        computed: {
            photoUrl() {
                return this.me.avatars.s2;
            },
            isFirstTime() {
                let key = `serious_profile_preq`;
                return !localStorage.getItem(key);
            },
            ...mapState('vip', ['hasVip']),
            ...mapState('feed', ['moderationStatus']),
            ...mapGetters('feed', ['needUploadPhoto', 'hasSupportAnswer'])
        },
        created() {
            getInfo().then(response => {
                let info = response.objects[0];
                if (info.avatars) {
                    this.$store.commit('updateUserAvatar', info.avatars);
                }
            });
        },
        mounted() {
            this.$nativeStat.trackFirebaseEvent('item_action', {screen: 'My Profile', action: 'View'});
            this.$nativeStat.trackMetricaEvent('item_action', {'My Profile': 'View'});
            this.$nativeStat.trackAppsFlyerEvent('fs_action', {af_content_type: 'My Profile', ap_action: 'View'});
        },
        methods: {
            previewProfile() {
                feed.openProfile({id: this.me.id});
            },
            editProfile() {
                feed.editProfile();
            },
            showOffer() {
                feed.showVipFullOffer({source: SOURCE_VIP_MY_PROFILE});
            },
            settings() {
                feed.showSettings();
            }
        }
    }
</script>
