<template>
    <div class="messenger">
        <mutual></mutual>
        <div class="messenger__tabs">
            <div
                    v-for="(tab, index) in tabs"
                    :key="index"
                    :class="['messenger__tab', tab.style, {active: currentTabId === index}]"
                    @click="currentTabId = index"
            >{{tab.title}}
            </div>
        </div>
        <contacts :extra-params="extraParams" :settings="settings" @select="openContact($event)"></contacts>
    </div>
</template>

<script>
    import Mutual from '@/components/messenger/Mutual';
    import Contacts from '@/components/messenger/Contacts';

    export default {
        components: {
            Mutual,
            Contacts
        },
        data() {
            return {
                tabs: [
                    {
                        title: this.$t('messenger.tab_all'),
                        params: {},
                        settings: {
                            dialogsPromo: true
                        }
                    },
                    {
                        title: this.$t('messenger.tab_online'),
                        params: {filter: 'online'},
                        settings: {
                            emptyMessage: this.$t('messenger.online_empty'),
                            dialogsPromo: false
                        },
                        style: 'green'
                    }
                ],
                currentTabId: 0
            };
        },
        computed: {
            extraParams() {
                return this.tabs[this.currentTabId].params;
            },
            settings() {
                return this.tabs[this.currentTabId].settings;
            }
        },
        mounted() {
            this.$nativeStat.trackFirebaseEvent('item_action', {screen: 'Messenger Chat List', action: 'View'});
            this.$nativeStat.trackMetricaEvent('item_action', {'Messenger Chat List': 'View'});
            this.$nativeStat.trackAppsFlyerEvent('fs_action', {
                af_content_type: 'Messenger Chat List',
                ap_action: 'View'
            });
        },
        methods: {
            openContact(contact) {
                this.$nav.push('/app/messenger/' + contact.id);
            }
        }
    };
</script>
