<template>
    <div class="feed-controls">
        <button class="btn btn-action btn-action_dislike ripple_white" v-touch @click="$emit('clickDislike')">
            <i class="ico ico_cross"></i>
        </button>
        <button class="btn btn-action btn-action_like ripple_white" v-touch @click="$emit('clickLike')">
            <i class="ico ico_heart"></i>
        </button>
    </div>
</template>

<script>
    import {mapState, mapGetters} from 'vuex';

    export default {
        name: 'Controls',
        props: ['isAvailableToChat'],
        computed: {
            ...mapState('feed', ['boost']),
            ...mapGetters('feed', ['needUploadPhoto'])
        }
    }
</script>
