import messenger from '../../messenger/';
import Vue from 'vue';

const DIALOG_STATUS_MINE_ONLY = 1;
const DIALOG_STATUS_DEFAULT = 3;
const DIALOG_STATUS_MAYBE_MINE_ONLY = 5;

export default {
    namespaced: true,
    state: {
        contacts: [],
        contactsOffset: 0,
        contactsLimit: 20,
        contactsExtraParams: {},
        contactsIsLoading: false,
        contactsHasNext: true,

        openedContact: null,
        openedContactAllowedToCorrespond: true,

        messages: [],
        messagesLimit: 20,
        messagesHasPrev: true,
        messagesIsLoading: false,
        messageIsSending: false
    },
    getters: {
        contactsRequestParams(state) {
            return {
                offset: state.contactsOffset,
                limit: state.contactsLimit,
                ...state.contactsExtraParams
            };
        },
        messagesRequestParams(state) {
            const params = {
                limit: state.messagesLimit,
                contactId: state.openedContact.id
            };

            if (state.messages.length) {
                params.before_time_id = state.messages[0].id;
            } else {
                params.offset = 0;
            }

            return params;
        },
        getContactById: state => id => {
            for (let i = 0; i < state.contacts.length; i++) {
                if (parseInt(state.contacts[i].id) === id) {
                    return state.contacts[i];
                }
            }
        }
    },
    mutations: {
        addNewContact(state, contact) {
            state.contacts = [contact, ...state.contacts];
        },
        cleanMessages(state) {
            state.messages = [];
            state.messagesOffset = 0;
            state.messagesIsLoading = false;
            state.messageIsSending = false;
            state.messagesHasPrev = true;
            state.openedContactAllowedToCorrespond = true;
        },
        readMineMessages(state, contactId) {
            if (state.openedContact && contactId == state.openedContact.id) {
                state.openedContact.unread = false;
            }

            for (let existed of state.contacts) {
                if (existed.id == contactId) {
                    existed.unread = false;
                }
            }
        },
        resetNewCounter(state, contactId) {
            if (state.openedContact && contactId == state.openedContact.id) {
                state.openedContact['new'] = 0;
            }

            for (let i = 0; i < state.contacts.length; i++) {
                if (state.contacts[i].id == contactId) {
                    Vue.set(state.contacts[i], 'new', 0);
                    break;
                }
            }
        },
    },
    actions: {
        loadContactsBatch(context) {
            if (!context.state.contactsHasNext) {
                return;
            }
            context.state.contactsIsLoading = true;
            return messenger.dataSource.getContacts(context.getters.contactsRequestParams)
                .then(response => {
                    context.state.contacts.push(...response.contacts);
                    context.state.contactsIsLoading = false;
                    context.state.contactsOffset += context.state.contactsLimit;
                    context.state.contactsHasNext = response.hasNext;
                });
        },
        loadContactsWithNewExtraParams(context, payload) {
            context.state.contactsExtraParams = payload;
            context.state.contacts = [];
            context.state.contactsHasNext = true;
            context.state.contactsOffset = 0;
            return context.dispatch('loadContactsBatch');
        },
        loadMessagesBatch(context) {
            if (!context.state.messagesHasPrev) {
                return;
            }
            context.state.messagesIsLoading = true;
            return messenger.dataSource.getMessages(context.state.openedContact, context.getters.messagesRequestParams)
                .then(response => {
                    context.state.messages.unshift(...response.messages);
                    context.state.messagesIsLoading = false;
                    context.state.messagesHasPrev = response.hasPrev;
                });
        },
        addNewMessage(context, {contactId, message}) {
            let contact;

            for (let i = 0; i < context.state.contacts.length; i++) {
                if (parseInt(context.state.contacts[i].id) === contactId) {
                    contact = context.state.contacts[i];
                    context.state.contacts.splice(i, 1);
                    break;
                }
            }

            contact.lastmess = message;

            if (message.author_id === contactId) {
                let prevValue = contact.new;
                Vue.set(contact, 'new', prevValue + 1);
            }

            if (message.author_id === messenger.userId && context.state.openedContact) {
                context.state.openedContact.has_my_messages = true;
            }

            context.state.contacts = [contact, ...context.state.contacts];

            if (context.state.openedContact && parseInt(context.state.openedContact.id) === contactId) {
                context.state.messages.push(message);
                context.state.openedContact.lastmess = message;
                context.state.openedContact.unread = message.author_id !== contactId;

                if (message.author_id == contactId) {
                    context.dispatch('markReaded', contactId);
                }
            }
        },
        postMessage(context, params) {
            return messenger.dataSource.postMessage({
                contactId: context.state.openedContact.id,
                ...params
            });
        },
        openContact(context, contact) {
            context.commit('cleanMessages');
            context.state.openedContact = contact;
        },
        closeContact(context) {
            context.commit('cleanMessages');
            context.state.openedContact = null;
        },
        markReaded(context, contactId) {
            return messenger.dataSource.markReaded(contactId).then(() => {
                context.commit('resetNewCounter', contactId);
            });
        },
        removeContact(context, contactId) {
            return messenger.dataSource.removeContact(contactId).then(() => {
                if (context.state.openedContact && contactId === context.state.openedContact.id) {
                    context.dispatch('closeContact');
                }

                for (let i = 0; i < context.state.contacts.length; i++) {
                    if (context.state.contacts[i].id === contactId) {
                        context.state.contacts.splice(i, 1);
                        break;
                    }
                    context.state.contacts = [...context.state.contacts];
                }
            });
        }
    }
};
