<template>
    <div class="signup-step">
        <div>
            <signup-step-back/>

            <div class="signup-step__title">{{$t('signup.name')}}</div>

            <div class="signup-step__field">
                <input class="signup-step__input" :class="{'signup-step__input_invalid': error != null}"
                       :placeholder="$t('signup.name_placeholder')"
                       type="text"
                       :value="userName"
                       @focus="focused = true" @blur="focused = false"
                       @input="input"
                       @keyup.enter="submit"
                       ref="inputField">
                <div v-if="error" class="signup-step__invalid">{{error}}</div>
            </div>
        </div>

        <div class="btn signup-step__btn ripple ripple_white" :class="{'signup-step__btn_active': checkName() !== false}" @click="nextStep">{{$t('done')}}</div>
    </div>
</template>

<script>
    import SignupStepBack from '@/components/signup/SignupStepBack';
    import {mapGetters, mapState} from 'vuex';

    export default {
        name: "SignupName",
        components: {SignupStepBack},
        data(){
            return {
                focused: false,
                error: null,
                userName: '',
                isBtnActive: false
            }
        },
        computed: {
            ...mapGetters('signup', ['params'])
        },
        created(){
            this.userName = this.params.name;
        },
        mounted(){
            this.$refs.inputField.focus();
        },
        methods: {
            input(event){
                this.userName = event.target.value;
            },
            back(){
                this.$nav.go(-1);
            },
            nextStep(){
                if (this.checkName()){
                    this.setName();
                }
            },
            checkName(){
                let isValid = false;

                if(this.userName.length > 0){
                    this.isBtnActive = true;

                    isValid = true;
                }

                return isValid;
            },
            setName(){
                this.$store.commit('signup/setName', this.userName);

                this.$nav.push('/app/signup/age');
            }
        }
    }
</script>